import { Component } from '@angular/core';
import { NavParams, Platform, NavController } from 'ionic-angular';
import { Storage } from '@ionic/storage';


@Component({
  selector: 'page-about-us',
  templateUrl: 'about-us.html',
})
export class AboutUs {
  language:any;
  titles:any =[];
  english: any =[];
  punjabi: any =[];
  styleCustom:any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage) {
// this.message = "       13  1946   ,    ,     .                    .     1974    ,      .                .";
// this.message1="            .                .";

this.english= { title: 'Dr. Dharambir Agnihotri', Designation: 'MLA of Tarn Taran', pagetitle:'About MLA', message: 'He was born on 1946, March 13 at village Sheron, Tehsil & Distt Taran Taran, Punjab. His fathers name is Sh. Madho Ram and mothers name is Shrimati Ravail Devi. He completed his graduation from Tibbia college, New Delhi. In 1974, He married to Kiran Agnihotri. Dr. dharambir agnihotri is known as a very honest and down to earth leader. He is popular among every one for his simple and soft spoken nature . He is serving the humanity with the noble profession of doctor. He is considered very close to  chief minister of Punjab Capt. Amarinder singh.'};
this.punjabi = { title: '  ', Designation: '   ( )', pagetitle:' ', message: '     13  1946   ,     ,      .                    .     1974    ,      .                  .            .               .'};
this.storage.get('lang').then((lang) => {
  this.language= lang;
  if(this.language == 'english'){
    this.titles = this.english;   
    this.styleCustom = "parah-1";
  }else{
    this.titles = this.punjabi;
    this.styleCustom = "parah-1 custom-FontPunjabi";
  }
})

  }
  
}



